<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-form v-if="!isLoading">
      <b-card
        header="Update Attendee Details"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-table
          table-variant="info"
          align="left"
          :items="[attendee]"
          :fields="['first_name', 'last_name', 'employee_number']"
        >
        </b-table>
        <b-table
          table-variant="secondary"
          align="left"
          :items="[ceremony]"
          :fields="ceremonyFields"
        >
          <template #cell(scheduled_datetime)="row">
            {{ row.item.scheduled_datetime.toLocaleString() }}
          </template>

        </b-table>
        <b-container fluid>
          <b-row>
            <InputAccommodations id="recipient" header="Recipient Options" />
          </b-row>
          <b-row>
            <b-form-checkbox v-model="attendee.has_guest" name="has-guest-toggle" switch>
              Add Guest: <b>{{ attendee.has_guest ? 'Yes' : 'No' }}</b>
            </b-form-checkbox>
          </b-row>
          <b-row>
            <InputAccommodations v-if="attendee.has_guest" id="guest" header="Guest Options" />
          </b-row>
          <b-row>

            <b-button
              class="mt-2"
              variant="success"
              :disabled="!validate() || processing"
              @click="submit()">
              Update Attendee Details
              <b-spinner class="ml-3" small v-if="processing" />
            </b-button>

            <b-button
              class="mt-2 ml-2"
              variant="info"
              :disabled="processing"
              @click="reroute(`/attendees/list/${ceremony.id}`)">
              Attendee List
            </b-button>

            <b-button
              class="mt-2 ml-2"
              variant="dark"
              :disabled="processing"
              @click="reroute('/ceremonies')">
              Manage Ceremonies
            </b-button>
          </b-row>
        </b-container>
      </b-card>
    </b-form>
  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { get, put } from "@/services/api.services";
import InputAccommodations from "../../components/InputAccommodations";

export default {
  head: {
    title: 'Register User',
  },
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader, InputAccommodations
  },
  data() {
    return {
      processing: false,
      ceremony: null,
      ceremonyFields: [
        {key: 'location_name', label: 'Venue'},
        {key: 'scheduled_datetime', label: 'Date/Time'}
      ]
    }
  },
  computed: {
    attendee() {
      return this.$store.getters.getFormData
    },
    isLoading() {
      return this.$store.getters.isLoading
    }
  },
  methods: {
    validate() {
      return true
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async submit() {
      this.processing = true

      const [error,] = await put(`attendees/update/${this.$route.params.id}`, this.attendee)

      if (error) {
        this.$store.commit('setMessage', {
            text: 'Attendee could not be saved. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        // successful update
        this.$store.commit('setMessage', {
          text: this.isCreate
            ? 'Ceremony successfully created'
            : 'Ceremony successfully updated',
          type: 'success'
        })
        // redirect to manage users
        this.isCreate ? await this.$router.push(`/ceremonies/`) : ()=>{}
      }

      this.processing = false
    }
  },
  async beforeCreate() {
    this.$store.commit('setLoading', true)

    // initialize recipient data
    const [error, initData] = await get(`attendees/show/${this.$route.params.id}` || '')

    console.log(initData)

    if (error) {
      this.$store.commit('setMessage', error)
      this.error = error.text
    }
    else {
      // get attendee details
      const {recipient=null, guest=null, accommodations=[], ceremony={}} = initData || {}

      // initialize attendee data
      await this.$store.commit('setFormData', {
        id: recipient.id || '',
        first_name: recipient.first_name || '',
        last_name: recipient.last_name || '',
        employee_number: recipient.employee_number || '',
        recipient: {
          accessibility: accommodations
            .filter(accom => accom.type === 'accessibility')
            .map(accom => {
            return accom.id
          }),
          dietary: accommodations
            .filter(accom => accom.type === 'dietary')
            .map(accom => {
            return accom.id
          })
        },
        has_guest: !!guest,
        guest: guest ? {
          accessibility: (guest.accommodations || [])
            .filter(accom => accom.type === 'accessibility')
            .map(accom => {
              return accom.id
            }),
            dietary: (guest.accommodations || [])
            .filter(accom => accom.type === 'dietary')
            .map(accom => {
              return accom.id
            })
        } : {
          accessibility: [],
          dietary: []
        }
      })

      // set ceremony info
      this.ceremony = ceremony
    }
    this.$store.commit('setLoading', false)
  }
}
</script>
