<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <ModalView :title="`View Attendee Details`" :item="selected"/>

    <b-container v-if="!isLoading" fluid>
      <b-alert v-if="!isAdmin" variant="info" show>Sign in to view this page.</b-alert>
      <b-row>
        <ActionRecipients
          :loader="loadData"
          :selectAll="selectAllRows"
          :clearSelected="clearSelected"
        />
      </b-row>
      <b-row>
        <b-table
          table-variant="secondary"
          :items="[ceremony]"
          :fields="ceremonyFields"
          small
        >
          <template #cell(scheduled_datetime)="row">
            {{ row.item.scheduled_datetime.toLocaleString('en-CA', {
            timeZone: "America/Vancouver",
            hour12: true
          }) }}
          </template>

        </b-table>
      </b-row>
      <b-row>
        <b-col cols="3">
          <b-form-group
            label="Status"
            label-for="table-select-filter-category"
            label-cols="3"
          >
            <b-form-select
              id="table-select-filter-category"
              v-model="selectCategory"
              :options="categories || []"
              class="mb-3"
              :disabled="this.processing"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col cols="9" class="text-right">
          <b-spinner class="m-1" small v-if="this.processing" />
          <b-button class="m-1" size="sm" @click="clearFilters">Clear Filters</b-button>
          <b-button
            size="sm"
            class="m-1"
            variant="info"
            :disabled="this.processing"
            @click="() => {this.loadData()}"
          >
            <BIconArrowRepeat /> Refresh
          </b-button>
          <b-button
            size="sm"
            variant="info"
            class="m-1"
            @click="reroute(`/ceremonies/edit/${ceremony.id || ''}`)">
            Edit Ceremony
          </b-button>
          <b-button
            size="sm"
            variant="dark"
            class="m-1" @click="reroute(`/ceremonies`)">
            Manage Ceremonies
          </b-button>
        </b-col>
      </b-row>
    </b-container>

    <b-table
      :items="recipients || []"
      :fields="fields"
      striped
      :filter="criteria"
      :filter-function="applyFilter"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      sort-icon-left
      responsive="sm"
      :busy.sync="loading"
      small
      id="attendees-table"
      ref="selectableTable"
      :selectable="true"
      @row-selected="onRowSelected"
      selected-variant="info"
    >

      <template #cell(status)="row">
        <b-badge class="m-1" :variant="getStatus(row.item.status)">
          {{ lookup('assignments', row.item.status) }}
        </b-badge>
      </template>

      <template #cell(guest)="row">
        {{row.item.guest ? 'Yes' : 'No'}}
      </template>

      <template #cell(attendee)="row">
        <b-list-group>
          <b-list-group-item
            class="small"
            v-for="(attendance, index) in row.item.attendee.filter(a => {return a.ceremonies_id !== ceremony.id})"
            v-bind:key="index"
          >
            <a :href="`/admin/attendees/list/${attendance.ceremonies.id}`">
              {{ lookup('assignments', attendance.status) }}: {{
                new Date(attendance.ceremonies.scheduled_datetime).toLocaleString()
              }}
            </a>
          </b-list-group-item>
        </b-list-group>
      </template>

      <template #cell(details)="row">
        <div class="text-right">
        <b-button
          size="sm"
          class="m-1"
          v-b-modal.modal-view-item
          @click="selectItem(row.item)"
        >
          View
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          :disabled="row.item.status !== 'attending'"
          target="_blank"
          :href="`/admin/attendees/edit/${row.item.attendee_id}`"
        >
          Edit
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          :href="`/admin/recipients/view/${row.item.id}`"
          target="_blank"
        >
          Recipient Profile
        </b-button>
        </div>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import reportServices from "@/services/reports.services";
import {get} from "@/services/api.services";
import optionsServices from '@/services/options.services'
import ModalView from '@/components/ModalView'
import { BIconArrowRepeat } from 'bootstrap-vue'
import ActionRecipients from "@/components/ActionRecipients";

export default {
  name: 'reports-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader, ModalView, BIconArrowRepeat, ActionRecipients
  },
  data () {
    return {
      loading: false,
      processing: false,
      error: false,
      message: '',
      sortBy: 'last_name',
      sortDesc: false,
      selectCategory: '',
      categories: optionsServices.get('assignments'),
      selected: '',
      fields: [
        {key: 'status', label: 'Status', sortable: true},
        {key: 'first_name', label: 'First Name', sortable: true},
        {key: 'last_name', label: 'Last Name', sortable: true},
        {key: 'guest', label: 'With Guest', sortable: true},
        {key: 'attendee', label: 'Other Attendance', sortable: false},
        {key: 'details', label: '', sortable: false}
      ],
      ceremonyFields: [
        {key: 'location_name', label: 'Venue'},
        {key: 'scheduled_datetime', label: 'Date/Time'},
        {key: 'total_waitlisted', label: 'Waitlisted'},
        {key: 'total_assigned', label: 'Assigned'},
        {key: 'total_invitees', label: 'Invited'},
        {key: 'total_expired', label: 'Expired'},
        {key: 'total_attending_recipients', label: 'Confirmed'},
        {key: 'total_guests', label: 'Guests'},
        {key: 'total_attending', label: 'Confirmed (+ Guests)'},
        {key: 'total_declined', label: 'Declined'},
      ],
      ceremony: null,
      recipients: [],
      guests: [],
      totals: {
        total_recipients: 0,
        total_guests: 0,
        total_vips: 0,
        total_attendees: 0
      }
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    reports () {
      return reportServices.reports() || []
    },
    criteria () {
      return {
        category: this.selectCategory
      }
    },
  },
  methods: {
    onRowSelected(items) {
      this.$store.commit('setSelected', items)
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows()
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected()
    },
    clearFilters() {
      this.selectCategory = ''
    },
    applyFilter(row) {
      return !this.criteria.category || row.status === this.criteria.category
    },
    selectItem(item) {
      const updatedTS = new Date(item.updated_at)
      const createdTS = new Date(item.created_at)
      // filter attendee data for viewing
      this.selected = {
        first_name: item.first_name,
        last_name: item.last_name,
        employee_number: item.employee_number,
        status: this.lookup('assignments', item.status),
        options: (item.accommodations || []).map(accommodation => {
          return `${accommodation.short_name} (${accommodation.type})`
        }).join('; '),
        has_guest: item.status === 'attending' && item.guest ? 'Yes' : 'No',
        guest_options: this.guests.filter(guest => {return guest.attendable_id === item.guest}).map(guest => {
          return (guest.accommodations || {}).map(accommodation => {
            return `${accommodation.short_name} (${accommodation.type})`
          }).join('; ')
        }).join(),
        created_at: createdTS,
        updated_at: updatedTS,
      }
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async loadData () {
      this.processing = true
      this.error = false

      // get attendees data
      const [error, attendees] = await get(`attendees/list/${this.$route.params.id || ''}`)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      // unpack API response data
      const {
        ceremony={},
        guests=[],
        recipients=[],
        counts={
          total_waitlisted: 0,
          total_assigned: 0,
          total_invitees: 0,
          total_expired: 0,
          total_attending_recipients: 0,
          total_guests: 0,
          total_attending: 0,
          total_declined: 0,
          total_vips: 0,
        },
        total_vips=0
      } = attendees || {}
      // map API response data to local state
      this.ceremony = ceremony
      this.recipients = recipients.map(recipient => {
        return {
          id: recipient.attendable.id,
          attendee_id: recipient.id,
          first_name: recipient.attendable.first_name,
          last_name: recipient.attendable.last_name,
          employee_number: recipient.attendable.employee_number,
          ceremony_opt_out: recipient.attendable.ceremony_opt_out,
          is_declared: recipient.attendable.is_declared,
          ceremonies_id: recipient.ceremonies_id,
          attendee: recipient.attendee,
          accommodations: recipient.accommodations,
          status: recipient.status,
          guest: recipient.status === 'attending' ? recipient.guest : null,
          created_at: recipient.created_at,
          updated_at: recipient.updated_at
        }
      })
      this.guests = guests

      // update ceremony counts
      this.ceremony.total_waitlisted = counts ? counts.total_waitlisted : 0
      this.ceremony.total_assigned = counts ? counts.total_assigned : 0
      this.ceremony.total_invitees = counts ? counts.total_invitees : 0
      this.ceremony.total_expired = counts ? counts.total_expired : 0
      this.ceremony.total_attending_recipients = counts ? counts.total_attending_recipients : 0
      this.ceremony.total_guests = counts ? counts.total_guests : 0
      this.ceremony.total_attending = counts ? counts.total_attending : 0
      this.ceremony.total_declined = counts ? counts.total_declined : 0
      this.ceremony.total_vips = total_vips || 0

      // console.log('Ceremony', this.ceremony)
      // console.log('Recipient', this.recipients)
      // console.log('Guests', this.guests)
      this.processing = false
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    getStatus(status) {
      const variants = {
        assigned: 'info',
        waitlisted: 'warning',
        invited: 'dark',
        attending: 'success',
        declined: 'danger',
        expired: 'warning',
      }
      return variants[status] || 'secondary'
    },
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    // retrieve attendees for this ceremony
    await this.loadData()
    this.$store.commit('setLoading', false)
  }
}
</script>
