<template>
  <div class="page-unauthorized">
    <b-jumbotron header='401 - Unauthorized' lead="You are restricted from accessing this page." />
    <b-container fluid>
      <p><b-button variant="info" type="button" @click="reroute('/login')">Sign In</b-button></p>
    </b-container>
  </div>
</template>

<script>
export default {
  methods: {
    async reroute(uri) {
      // check if already at url to avoid redundant navigation
      if (uri !== this.$route.path) await this.$router.push(uri)
    },
  },
  async mounted () {
    this.$store.commit('setLoading', false)
  }
};
</script>
