<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <ModalView
      :title="`View ${single || ''} Details`"
      :item="selected"
    />

    <ModalDelete
      :title="`Delete ${single || ''}`"
      :item="selected"
      :url="`${model}/delete/${selected.id || ''}`"
      :rerouteURL="`${model}`"
      :loader="loadData"
    />

    <b-container fluid>
      <b-button
        size="sm"
        class="mb-3"
        variant="success"
        :disabled="this.processing"
        @click="reroute(`/${model}/create`)"
      >
        Create {{single}}
      </b-button>
      <b-button
        size="sm"
        class="ml-2 mb-3"
        variant="dark"
        :disabled="this.processing"
        @click="() => {this.loadData()}"
      >
        <BIconArrowRepeat /> Refresh
      </b-button>
      <b-spinner class="ml-3" small v-if="this.processing" />
    </b-container>

    <b-table
      striped
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      sort-icon-left
      small
      id="data-table"
      :items="items"
      :fields="fields"
      responsive="sm"
      :busy.sync="loading"
    >
      <template #cell(scheduled_datetime)="row">
        {{ new Date(row.item.scheduled_datetime).toLocaleString() }}
      </template>
      <template #cell(created_at)="row">
        {{ new Date(row.item.created_at).toLocaleString() }}
      </template>
      <template #cell(updated_at)="row">
        {{ new Date(row.item.updated_at).toLocaleString() }}
      </template>
      <template #cell(details)="row">
        <div class="text-right">
          <b-button
            size="sm"
            class="m-1"
            v-b-modal.modal-view-item
            @click="selectItem(row.item)"
          >
            View
          </b-button>
          <b-button
            size="sm"
            class="m-1"
            @click="reroute(`/${model}/edit/${row.item.id}`)"
          >
            Edit
          </b-button>
          <b-button
            size="sm"
            class="m-1"
            variant="secondary"
            v-b-modal.modal-delete-item
            @click="selectItem(row.item)"
          >
            Delete
          </b-button>
          <b-button
            v-if="model === 'ceremonies'"
            size="sm"
            class="m-1"
            variant="info"
            :disabled="row.item.total_attendees === 0"
            @click="reroute(`/attendees/list/${row.item.id}`)"
          >
            View Attendees
          </b-button>
        </div>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import ModalView from '@/components/ModalView'
import ModalDelete from '@/components/ModalDelete'
import {get} from "@/services/api.services";
import optionsServices from "@/services/options.services";
import { BIconArrowRepeat } from 'bootstrap-vue'

export default {
  name: `item-list`,
  props: ['header', 'lead', 'mode', 'model', 'single'],
  components: {
    PageHeader,
    ModalView,
    ModalDelete,
    BIconArrowRepeat
  },
  data () {
    return {
      processing: false,
      loading: false,
      error: false,
      message: '',
      sortBy: 'updated_at',
      sortDesc: true,
      selected: '',
      items: [],
      fields: optionsServices.get(this.$props.model)
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    }
  },
  methods: {
    selectItem(item) {
      this.selected = item;
    },
    async loadData () {
      this.processing = true
      this.error = false
      const [error, data] = await get(`${this.$props.model}/list`)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      this.items = data
      this.processing = false
    },
    async reroute(uri) {
      await this.$router.push(uri)
    }
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    // load global options
    await this.$store.dispatch('loadOptions')
    // load API data
    await this.loadData()
    this.$store.commit('setLoading', false)
  }
}
</script>
