<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-form v-if="!isLoading">
      <b-card
        :header="isCreate ? 'Create Accommodation' : 'Update Accommodation'"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-form-group>

          <b-form-group
            id="input-accommodation-type"
            label="Select Accommodation Type"
            label-for="input-accommodation-milestone"
            class="mb-4"
          >
            <b-form-select
              id="input-accommodation-type"
              v-model="accommodation.type"
              :options="types"
              :state="validate('type')"
            >
            </b-form-select>
            <b-form-invalid-feedback :state="validate('type')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="fieldset-accommodation-name"
            label="Accommodation Name"
            label-for="input-accommodation-name"
          >
            <b-form-input
              id="input-accommodation-fullname"
              v-model="accommodation.full_name"
              placeholder="Full name of accommodation"
              :state="validate('fullname')"
            />
            <b-form-invalid-feedback :state="validate('fullname')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="fieldset-accommodation-shortname"
            label="Short Name"
            label-for="input-accommodation-name"
          >
            <b-form-input
              id="input-accommodation-shortname"
              v-model="accommodation.short_name"
              placeholder="Code or shortened name of accommodation"
              :state="validate('short_name')"
            />
            <b-form-invalid-feedback :state="validate('short_name')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="fieldset-accommodation-description"
            label="Description"
            label-for="input-accommodation-description"
            description="Use '\n' for line breaks."
          >
            <b-form-textarea
              id="input-accommodation-description"
              v-model="accommodation.description"
            />
          </b-form-group>

          <b-button
            class="mt-2"
            variant="info"
            :disabled="!validate('all') || processing"
            @click="submit()">
            {{ isCreate ? 'Create' : 'Update'}}
            <b-spinner class="ml-3" small v-if="processing" />
          </b-button>

          <b-button
            class="mt-2 ml-2"
            variant="secondary"
            :disabled="processing"
            @click="reroute('/accommodations')">
            Return to Accommodations
          </b-button>

        </b-form-group>
      </b-card>
    </b-form>
  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { get, post, put } from "@/services/api.services";
import optionServices from '@/services/options.services'

export default {
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader
  },
  data() {
    return {
      processing: false,
      accommodation: null,
      types: optionServices.get('accommodation-types')
    }
  },
  computed: {
    isLoading() {
      return this.$store.getters.isLoading || this.$store.getters.getOrganizations.length === 0
    },
    isCreate () {
      return this.mode === 'create'
    },
    validate(){
      return (field) => {
        const fields = {
          type: ()=>{
            return !!this.accommodation.type
          },
          fullname: ()=>{
            return !!this.accommodation.full_name
          },
          short_name: ()=>{
            return !!this.accommodation.short_name
          }
        }
        // check if all fields validate
        if (field === 'all') return Object.keys(fields).filter(key => {
          return !fields[key]()
        }).length === 0

        if (!fields[field]) return false;
        return !!fields[field]();
      }
    }
  },
  methods: {
    async submit() {
      this.processing = true

      console.log(this.accommodation)

      const [error,] = this.isCreate
        ? await post(`accommodations/create`, this.accommodation)
        : await put(`accommodations/update/${this.$route.params.id}`, this.accommodation)

      if (error) {
        this.$store.commit('setMessage', {
            text: 'Accommodation could not be saved. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        // successful update
        this.$store.commit('setMessage', {
          text: this.isCreate
            ? 'Accommodation successfully created'
            : 'Accommodation successfully updated',
          type: 'success'
        })
        // redirect to manage accommodations
        this.isCreate ? await this.$router.push(`/accommodations/`) : ()=>{}
      }

      this.processing = false
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    lookup(key, value) {
      return optionServices.lookup(key, value)
    },
  },
  async created() {
    this.$store.commit('setLoading', true)

    // initialize data
    const [error, data] = this.isCreate
      ? [null, {}]
      : await get(`accommodations/show/${this.$route.params.id}`)

    if (error) this.$store.commit('setMessage', error)

    this.accommodation = {
      id: data.id || '',
      full_name: data.full_name || '',
      short_name: data.short_name || '',
      type: data.type || '',
      description: data.description || '',
    }

    this.$store.commit('setLoading', false)
  }
}
</script>
