<template>
  <b-container fluid v-if="!isLoading && isAdmin" class="p-2 rounded bg-secondary text-white">
    <b-modal
      id="modal-action"
      ref="modal-action"
      :title="lookup(type, selectAction).toString()"
      scrollable
      @ok="processAction()"
    >
      <template #modal-footer="{ ok, cancel }">
        <b-button :disabled="!selectOption" size="sm" variant="info" @click="ok()">
          Apply
        </b-button>
        <b-button size="sm" variant="secondary" @click="cancel()">
          Cancel
        </b-button>
      </template>
      <b-container class="p-2 rounded">
        <b-row>
          <b-col>
            <b-form-select
              id="table-select-action"
              v-model="selectOption"
              :options="options"
              :state="!!selectOption"
            ></b-form-select>
          </b-col>
        </b-row>
      </b-container>
    </b-modal>

    <b-modal
      id="modal-test-email"
      ref="modal-test-email"
      title="Enter Test Email"
      @ok="processAction()"
    >
      <template #modal-footer="{ ok, cancel }">
        <b-button :disabled="!validateEmail(selectEmail)" size="sm" variant="info" @click="ok()">
          Send
        </b-button>
        <b-button size="sm" variant="secondary" @click="cancel()">
          Cancel
        </b-button>
      </template>
      <b-container class="p-2 rounded">
        <b-row>
          <b-col>
            <b-form-group>
              <b-form-input
                id="table-select-test-email"
                v-model="selectEmail"
                :state="validateEmail(selectEmail)"
                placeholder="Enter Test Email"
              ></b-form-input>
            </b-form-group>
          </b-col>
        </b-row>
      </b-container>
    </b-modal>

    <b-row>
      <b-col cols="4">
        <b-form-select
          id="table-select-action"
          v-model="selectAction"
          :options="actions"
        ></b-form-select>
      </b-col>
      <b-col>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="info"
          :disabled="processing || !hasAction || !hasSelected"
          @click="applyAction"
        >
          Apply
        </b-button>
      </b-col>
      <b-col cols="6" class="text-right">
        <b-spinner v-if="processing" class="mr-3 ml-3" small />
        <span v-if="selectedItems.length > 0"> {{ selectedItems.length }} Item(s) Selected</span>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="dark"
          :disabled="processing"
          @click="selectAll">
          Select All
        </b-button>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="dark"
          :disabled="processing || !hasSelected"
          @click="clearSelected">
          Clear Selected
        </b-button>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="success"
          :disabled="!isAdmin"
          @click="reroute('/recipients/create')">Add Recipient</b-button>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>

import optionsServices from '@/services/options.services'
import {validateEmail} from '../services/validation.services';
import {put} from "../services/api.services";

export default {
  name: "modal-action",
  props: ['loader', 'selectAll', 'clearSelected'],
  data() {
    return {
      type: 'recipientActions',
      processing: false,
      selectAction: '',
      actions: optionsServices.get('recipientActions'),
      selectedActionProcess: null,
      selectOption: '',
      selectEmail: ''
    }
  },
  computed: {
    options() {
      return this.$store.getters.getCeremonies
    },
    selectedItems() {
      return this.$store.getters.getSelected || []
    },
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    hasAction() {
      return !!this.selectAction
    },
    hasSelected() {
      return this.selectedItems.length > 0
    },
  },
  methods : {
    async reroute(uri) {
      await this.$router.push(uri)
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    getURL() {
      // get url for selected action
      const found = this.actions.filter(item => item.value === this.selectAction)
      return found.length > 0 ? found[0].url : null
    },
    async applyAction() {
      // handle reminder emails
      if (this.selectAction === 'reminder') {
        return await this.processAction()
      }
      // handle test emails
      if (
        (this.selectAction === 'test-reminder'
        || this.selectAction === 'test-invitation'
        || this.selectAction === 'test-rsvp-confirmation') && this.validateAction()
      ) {
        return this.$bvModal.show('modal-test-email')
      }
      // handle other valid recipient actions
      if (this.validateAction()) {
        // reset, invite, RSVP confirmation do not require ceremony selection
        if (this.selectAction === 'reset'
          || this.selectAction === 'invited'
          || this.selectAction === 'rsvp-confirmation'
          || this.selectAction === 'attending'
          || this.selectAction === 'declined'
        ) {
          return await this.processAction()
        }
        // select ceremony
        this.$bvModal.show('modal-action')
      }
    },
    async processAction() {

      // get url for selected action
      const url = this.getURL()
      const data = {
        email: this.selectEmail,
        status: this.selectAction,
        ceremony: this.selectOption,
        attendees: this.selectedItems
      }

      // process method for requested action
      this.processing = true
      const [error,] = await put(url, data)
      if (error) this.$store.commit('setMessage', {
        text: 'An error occurred. Your action could not be completed',
        type: 'danger'
      })
      else {
        // successful action
        this.$store.commit('setMessage', {
          text: `Recipient(s) action ${data.status.toUpperCase()} was successful!`, type: 'success'
        })
      }
      // reset selections
      this.selectOption = ''
      this.selectAction = ''
      this.$store.commit('setSelected', [])
      this.clearSelected()
      // reload data
      await this.loader()
      this.processing = false
    },
    validateEmail(email) {
      return validateEmail(email)
    },
    validateAction() {
      // confirm no ceremony opt-out
      const hasCeremonyOptOut = this.selectedItems
        .filter(recipient => {return recipient.ceremony_opt_out}).length > 0

      // confirm registration is confirmed
      const isDraft = this.selectedItems
        .filter(recipient => {return !recipient.is_declared}).length > 0

      // (for waitlisting) check that attendee is not already assigned to ceremony
      const validWaitlist = this.selectedItems
        .filter(recipient => {
          return recipient.attendee.filter(attendee => {
            return attendee.status !== 'waitlisted' && attendee.ceremonies_id === this.selectOption
          }).length > 0
        }).length === 0

      // (for assignments) check attendee is not invited/declined/attending
      const validAssign = this.selectedItems
        .filter(recipient => {
          return recipient.attendee.filter(attendee => {
            return attendee.status === 'invited' || attendee.status === 'attending' || attendee.status === 'declined'
          }).length > 0
        }).length === 0

      // (for invitations) check if ALL attendees are assigned or invited to ceremonies
      const validInvitee = this.selectedItems
        .filter(recipient => {
          return recipient.attendee.filter(attendee => {
            return attendee.status === 'assigned' || attendee.status === 'invited' || attendee.status === 'expired'
          }).length > 0
        }).length === this.selectedItems.length

      // check if ALL attendees are attending ceremonies
      const validAttending = this.selectedItems
        .filter(recipient => {
          return recipient.attendee.filter(attendee => {
            return attendee.status === 'attending'
          }).length > 0
        }).length === this.selectedItems.length

      // check if ALL attendees are declining ceremonies
      const validDeclining = this.selectedItems
        .filter(recipient => {
          return recipient.attendee.filter(attendee => {
            return attendee.status === 'declined'
          }).length > 0
        }).length === this.selectedItems.length

      // draft registrations
      if (isDraft) {
        this.$store.commit('setMessage', {
          text: 'Draft registrations cannot have assigned ceremonies.',
          type: 'danger'
        })
        return false
      }
      // recipient has opted out of the ceremony
      if (hasCeremonyOptOut) {
        this.$store.commit('setMessage', {
          text: 'Selected recipients have opted out of attending ceremonies.',
          type: 'danger'
        })
        return false
      }

      // waitlisting only available to unassigned ceremonies
      if (this.selectAction === 'waitlisted' && !validWaitlist) {
        this.$store.commit('setMessage', {
          text: 'Attendee cannot be waitlisted. Please check their ceremony status.',
          type: 'danger'
        })
        return false
      }

      // assignments not available to invited/declined/attending attendees
      if (this.selectAction === 'assigned' && !validAssign) {
        this.$store.commit('setMessage', {
          text: 'Attendee cannot be assigned. Please check their ceremony status.',
          type: 'danger'
        })
        return false
      }

      // invitations only sent to assigned attendees (cannot re-invite)
      if ((this.selectAction === 'invited' || this.selectAction === 'test-invitation') && !validInvitee) {
        this.$store.commit('setMessage', {
          text: 'One or more selected attendees cannot be invited (or re-invited). Ensure each invitee is assigned a ceremony',
          type: 'danger'
        })
        return false
      }

      // change recipient status to declined
      if (this.selectAction === 'attending' && !validDeclining) {
        this.$store.commit('setMessage', {
          text: 'Attendee status cannot be updated to attending. Please ensure attendee has initially declined.',
          type: 'danger'
        })
        return false
      }

      // change recipient status to attending
      if (this.selectAction === 'declined' && !validAttending) {
        this.$store.commit('setMessage', {
          text: 'Attendee status cannot be updated to declined. Please ensure recipient is currently attending.',
          type: 'danger'
        })
        return false
      }

      // confirmations only sent to assigned attendees (cannot re-invite)
      if ((this.selectAction === 'rsvp-confirmation' || this.selectAction === 'test-rsvp-confirmation') && !validAttending) {
        this.$store.commit('setMessage', {
          text: 'One or more selected attendees cannot be confirmed. Ensure each invitee is attending a ceremony',
          type: 'danger'
        })
        return false
      }
      return true;
    },
  }
};
</script>
