<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-form v-if="!isLoading">

      <b-card
        header="Venue"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-form-group
          :id="`fieldset-location-name`"
          :label-for="`input-location-name`"
        >
          <b-form-input
            :id="`input-location-name`"
            v-model="ceremony.location_name"
            placeholder="Name of Venue"
          />
        </b-form-group>
      </b-card>

      <Address id="location_address" header="Address" label="Address" :required="false"  />
      <b-card
        header="Ceremony Date and Time"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >

        <b-form-group
          id="fieldset-ceremony-scheduled-date"
          label="Scheduled Date"
          label-size="md"
          label-class="font-weight-bold"
          label-for="input-ceremony-scheduled-date"
          class="mb-4"
        >
          <b-form-datepicker
            id="input-ceremony-scheduled-date"
            v-model="ceremony.date"
            :min="minDate"
            :max="maxDate"
            locale="en"
            :state="!!ceremony.date"
          />
          <b-form-invalid-feedback :state="!!ceremony.date">
            This field is required.
          </b-form-invalid-feedback>

          <b-form-group
            id="fieldset-ceremony-scheduled-time"
            label="Scheduled Time"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-ceremony-scheduled-time"
            class="mt-2 mb-4"
          >
            <b-form-timepicker
              id="input-ceremony-scheduled-time"
              v-model="ceremony.time"
              :min="minDate"
              :max="maxDate"
              locale="en"
              :state="!!ceremony.time && !!ceremony.date"
            />
            <b-form-invalid-feedback :state="!!ceremony.time && !!ceremony.date">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>
        </b-form-group>

          <b-form-group
            id="fieldset-ceremony-notes"
            label="Additional Information"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-ceremony-notes"
            class="mt-2 mb-4"
          >

          <b-form-textarea
            id="input-ceremony-notes"
            v-model="ceremony.notes"
            placeholder="Enter any additional notes for this ceremony."
            rows="3"
            max-rows="6"
          ></b-form-textarea>

          </b-form-group>

          <b-button
            class="mt-2"
            variant="info"
            :disabled="!validate() || processing"
            @click="submit()">
            {{ isCreate ? 'Create' : 'Update'}}
            <b-spinner class="ml-3" small v-if="processing" />
          </b-button>

          <b-button
            class="mt-2 ml-2"
            variant="secondary"
            :disabled="processing"
            @click="reroute('/ceremonies')">
            Return to Ceremonies
          </b-button>


      </b-card>
    </b-form>
  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { get, put, post } from "@/services/api.services"
import Address from '@/components/InputAddress'

export default {
  head: {
    title: 'Register User',
  },
  props: ['header', 'lead', 'mode'],
  components: {
    Address,
    PageHeader
  },
  data() {
    return {
      processing: false,
    }
  },
  computed: {
    ceremony() {
      return this.$store.getters.getFormData
    },
    isLoading() {
      return this.$store.getters.isLoading || this.$store.getters.getOrganizations.length === 0
    },
    isOrgContact() {
      return this.user.role === 'orgContact'
    },
    isCreate () {
      return this.mode === 'create'
    },
    minDate() {
      const n = -1
      const thisDate = new Date()
      return new Date(thisDate.setFullYear(thisDate.getFullYear() + n))
    },
    maxDate() {
      const n = 1
      const thisDate = new Date()
      return new Date(thisDate.setFullYear(thisDate.getFullYear() + n))
    },
  },
  methods: {
    validate() {
      return !!this.ceremony.date && !!this.ceremony.time
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async submit() {
      this.processing = true

      // concatenate date and time into string
      const datetime = `${this.ceremony.date} ${this.ceremony.time}`

      console.log('New Date/time:', datetime)

      const [error,] = this.isCreate
        ? await post(`ceremonies/create`, {
          scheduled_datetime: datetime,
          location_name: this.ceremony.location_name,
          location_address: this.ceremony.location_address,
          notes: this.ceremony.notes
        })
        : await put(`ceremonies/update/${this.$route.params.id}`, {
          id: this.ceremony.id,
          scheduled_datetime: datetime,
          location_name: this.ceremony.location_name,
          location_address_id: this.ceremony.location_address_id,
          location_address: this.ceremony.location_address,
          notes: this.ceremony.notes
        })

      if (error) {
        this.$store.commit('setMessage', {
            text: 'Ceremony could not be saved. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        // successful update
        this.$store.commit('setMessage', {
          text: this.isCreate
            ? 'Ceremony successfully created'
            : 'Ceremony successfully updated',
          type: 'success'
        })
        // redirect to manage users
        this.isCreate ? await this.$router.push(`/ceremonies/`) : ()=>{}
      }

      this.processing = false
    }
  },
  async created() {
    this.$store.commit('setLoading', true)

    // retrieve ceremony details
    const [error, data] = this.isCreate
      ? [null, {
        id: '',
        date: '',
        time: '',
        location_name: '',
        location_address: {
          street_address: '',
          community: '',
          postal_code: ''
        },
        notes: ''
      }]
      : await get(`ceremonies/show/${this.$route.params.id}` || '')

    if (error) {
      this.$store.commit('setMessage', error)
      this.error = error.text
    }
    else {
      // initialize ceremony data
      // - format date string (use current date if new ceremony)
      console.log('date/time:', data.scheduled_datetime)
      const datetime = this.mode === 'edit'
        ? new Date(data.scheduled_datetime.toLocaleString('en-CA', {timeZone: "America/Vancouver"}))
        : new Date()

      const day = datetime.getDate();
      const month = datetime.getMonth() + 1; // getMonth() returns month from 0 to 11
      const year = datetime.getFullYear();

      const initData = {
        id: data.id || '',
        date: datetime ? `${year}-${month}-${day}` : '',
        time: datetime ? datetime.toLocaleTimeString('en-CA', {
          timeZone: "America/Vancouver",
          hour12: false
        }) || '' : '',
        location_name: data.location_name || '',
        location_address_id: data.location_address_id || '',
        location_address: data.location_address ? data.location_address : {
          prefix: '',
          street_address: '',
          postal_code: '',
          community: '',
          notes: ''
        }
      }
      console.log(initData)
      this.$store.commit('setFormData', initData)
    }
    this.$store.commit('setLoading', false)
  }
}
</script>
