<template>
  <div>
    <ActionRecipients
      :loader="applyFilter"
      :selectAll="selectAll"
      :clearSelected="clearSelected"
    />
    <div v-if="!isLoading" >
      <b-container fluid class="mb-2 mt-2 p-2 rounded bg-secondary text-white">
        <b-row>
          <b-col sm="6">
            <p><span class="font-weight-bold pt-5">Results:</span>
              {{pagination.total}} Recipients <b-badge variant="warning" v-if="isFiltered">Filtered</b-badge></p>
          </b-col>
          <b-col sm="6" class="text-right">
            <b-button
              v-b-toggle.collapse-1
              variant="info"
              class="m-1 border-light font-weight-bold"
              size="sm"
            >
              <span class="when-open"><BIconX scale="1.5" /> Close</span>
              <span class="when-closed"><BIconFilterSquareFill /> Filter Recipients</span>
            </b-button>
          </b-col>
        </b-row>
      <b-collapse id="collapse-1" class="mt-2">
          <b-row>
            <b-col sm="2">
              <b-form-group
                label="Results Per Page"
                label-for="table-select-filter-results-per-page"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-results-per-page"
                  v-model="selectResultsPerPage"
                  :options="resultsPerPage"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Sort By"
                label-for="table-select-filter-sort-by"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-sort-by"
                  v-model="selectSortBy"
                  :options="sortBy"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Sort Order"
                label-for="table-select-filter-sort-order"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-sort-order"
                  v-model="selectSortOrder"
                  :options="sortOrder"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2" v-if="isAdmin">
              <b-form-group
                label="Status"
                label-for="table-select-filter-declared"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-declared"
                  v-model="selectStatus"
                  :options="statuses"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Historical"
                label-for="table-select-filter-historical"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-historical"
                  v-model="selectHistorical"
                  :options="historical"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>

          </b-row>
          <b-row>
            <b-col sm="3">
              <b-form-group
                label="Recipient Name"
                label-for="table-input-filter-name"
                label-size="sm"
              >
                <b-form-input
                  id="table-input-filter-name"
                  v-model="selectName"
                  placeholder="Search Recipient's First or Last Name"
                  size="sm"
                  @keydown.enter.native="handleEnter"
                />
                <p class="small">Enter the first <span class="font-weight-bold">OR</span> last name.</p>
              </b-form-group>
            </b-col>
            <b-col sm="3">
              <b-form-group
                label="Employee Number"
                label-for="table-input-filter-employee-number"
                label-size="sm"
              >
                <b-form-input
                  id="table-input-filter-employee-number"
                  v-model="selectEmployeeNumber"
                  placeholder="Search recipient's employee number"
                  size="sm"
                />
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Organization"
                label-for="table-select-filter-organization"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-organization"
                  v-model="selectOrganization"
                  :options="organizations"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Retirement"
                label-for="table-select-filter-retiring"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-retiring"
                  v-model="selectIsRetiring"
                  :options="isRetiring"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        <b-row>
          <b-col sm="2">
            <b-form-group
              label="Milestone"
              label-for="table-select-filter-milestone"
              label-size="sm"
            >
              <b-form-select
                id="table-select-filter-milestone"
                v-model="selectMilestone"
                :options="milestones"
                class="mb-3"
                size="sm"
              ></b-form-select>
            </b-form-group>
          </b-col>
            <b-col sm="2">
              <b-form-group
                label="Qualifying Year"
                label-for="table-select-filter-qualifying-year"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-qualifying-year"
                  v-model="selectQualifyingYear"
                  :options="qualifyingYears"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group
                label="Ceremony"
                label-for="table-select-filter-ceremony"
                label-size="sm"
              >
                <b-form-select
                  id="table-select-filter-ceremony"
                  v-model="selectCeremony"
                  :options="ceremonies"
                  size="sm"
                ></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        <b-row>
          <b-col class="text-right">
            <b-spinner v-if="processing" class="mr-3" small label="Loading"></b-spinner>
            <b-button
              class="m-1 border-light font-weight-bold"
              size="sm"
              variant="info"
              :disabled="processing"
              @click="applyFilter()">
              Apply Filters</b-button>
            <b-button
              class="m-1 border-light font-weight-bold"
              size="sm"
              variant="dark"
              @click="clearFilters"
              :disabled="processing">
              Clear Filters</b-button>
            <b-button
              class="m-1 border-light font-weight-bold"
              size="sm"
              variant="dark"
              @click="applyFilter()"
              :disabled="processing">
              <BIconArrowRepeat /> Refresh </b-button>
          </b-col>
        </b-row>
      </b-collapse>
      </b-container>
    </div>

    <b-container v-if="!isLoading">
      <b-row>
        <b-col align-self="center" class="overflow-auto text-center">
          <div>
            <b-button
              class="m-2"
              variant="outline-secondary"
              @click="applyFilter(1)">
              <BIconChevronDoubleLeft/>
            </b-button>
            <b-button
              v-for="(link, index) in pagination.links"
              v-bind:key="index"
              class="m-2"
              :variant="link.active ? 'info' : 'outline-info'"
              :disabled="!link.url"
              @click="applyFilter(getPageNum(link.url))">
              <BIconCaretLeft v-if="index === 0" />
              <span v-if="index !== 0 && index !== pagination.links.length - 1">
                {{ link.label }}
              </span>
              <BIconCaretRight v-if="index === pagination.links.length - 1" />
            </b-button>
            <b-button
              class="m-2"
              variant="outline-secondary"
              @click="applyFilter(pagination.links.length - 2)">
              <BIconChevronDoubleRight/>
            </b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>

import optionServices from "@/services/options.services";
import {
  BIconCaretLeft,
  BIconCaretRight,
  BIconChevronDoubleLeft,
  BIconChevronDoubleRight,
  BIconArrowRepeat,
  BIconFilterSquareFill,
  BIconX
} from "bootstrap-vue";
import ActionRecipients from "@/components/ActionRecipients";

export default {
  name: "filter-recipients-list",
  components: {
    ActionRecipients,
    BIconCaretRight,
    BIconCaretLeft,
    BIconChevronDoubleLeft,
    BIconChevronDoubleRight,
    BIconArrowRepeat,
    BIconFilterSquareFill,
    BIconX
  },
  props: {
    loader: Function,
    authorize: Boolean,
    selectAll: Function,
    clearSelected: Function
  },
  data() {
    return {
      processing: false,
      error: false,
      filtered: false,
      selectHistorical: '',
      selectStatus: '',
      selectMilestone: '',
      selectQualifyingYear: '',
      selectIsRetiring: '',
      selectCeremony: '',
      selectOrganization: '',
      selectResultsPerPage: '',
      selectName: '',
      selectEmployeeNumber: '',
      selectSortBy: '',
      selectSortOrder: '',
      historical: optionServices.get('isHistorical'),
      statuses: optionServices.get('status'),
      isRetiring: optionServices.get('isRetiring'),
      milestones: optionServices.get('milestones'),
      qualifyingYears: optionServices.get('qualifying-years'),
      resultsPerPage: optionServices.get('results-per-page'),
      sortBy: optionServices.get('sort-by'),
      sortOrder: optionServices.get('sort-order'),
    }
  },
  methods: {
    handleEnter(e) {
      if (e.which === 13) {
        this.applyFilter()
      }
    },
    getPageNum(url) {
      const urlArray = url.split('page=') || []
      return urlArray[urlArray.length - 1]
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async clearFilters() {
      this.selectHistorical = ''
      this.selectStatus = ''
      this.selectName = ''
      this.selectEmployeeNumber = ''
      this.selectIsRetiring = ''
      this.selectCeremony = ''
      this.selectMilestone = ''
      this.selectQualifyingYear = ''
      this.selectOrganization = ''
      this.selectResultsPerPage = ''
      this.selectSortBy = ''
      this.selectSortOrder = ''

      // reload data
      this.processing = true
      await this.loader()
      this.processing = false
      this.filtered = false
    },
    async applyFilter (pageNum=null) {
      this.processing = true
      const filters = []
      if (this.selectHistorical !== '') filters.push({
        key: 'historical', value: !!this.selectHistorical})
      if (this.selectStatus !== '') filters.push({
        key: 'status', value: !!this.selectStatus})
      if (this.selectName !== '') filters.push({
        key: 'name', value: this.selectName})
      if (this.selectEmployeeNumber !== '') filters.push({
        key: 'employee_number', value: this.selectEmployeeNumber})
      if (this.selectIsRetiring !== '') filters.push({
        key: 'retirement', value: !!this.selectIsRetiring})
      if (this.selectCeremony !== '') filters.push({
        key: 'ceremony', value: this.selectCeremony})
      if (this.selectMilestone !== '') filters.push({
        key: 'milestone', value: this.selectMilestone})
      if (this.selectQualifyingYear !== '') filters.push({
        key: 'qualifying_year', value: this.selectQualifyingYear})
      if (this.selectOrganization !== '') filters.push({
        key: 'organization', value: this.selectOrganization})
      if (this.selectResultsPerPage !== '') filters.push({
        key: 'results_per_page', value: this.selectResultsPerPage})
      if (this.selectSortBy !== '') filters.push({
        key: 'sort',
        value: `${this.selectSortBy}${
          this.selectSortOrder!==''
            ? '+' + this.selectSortOrder
            : ''
        }`
      })
      await this.loader(pageNum, filters)
      this.processing = false
      this.filtered = (
        this.selectName !== ''
        || this.selectEmployeeNumber !== ''
        || this.selectHistorical !== ''
        || this.selectStatus !== ''
        || this.selectIsRetiring !== ''
        || this.selectMilestone !== ''
        || this.selectQualifyingYear !== ''
        || this.selectCeremony !== ''
        || this.selectOrganization !== ''
      )
    },
  },
  computed: {
    hasFilters() {
      return this.selectName !== ''
        || this.selectEmployeeNumber !== ''
        || this.selectHistorical !== ''
        || this.selectStatus !== ''
        || this.selectIsRetiring !== ''
        || this.selectMilestone !== ''
        || this.selectQualifyingYear !== ''
        || this.selectCeremony !== ''
        || this.selectOrganization !== ''
        || this.selectResultsPerPage !== ''
        || this.selectSortBy !== ''
        || this.selectSortOrder !== ''
    },
    isFiltered() {
      return this.filtered
    },
    pagination() {
      return this.$store.getters.getPagination
    },
    user() {
      return this.$store.getters.getUser
    },
    ceremonies() {
      return this.$store.getters.getCeremoniesOptOut
    },
    organizations() {
      // filter organizations by recipient authorization
      const orgs = this.$store.getters.getOrganizations
      return this.user.role === 'orgContact'
        ? orgs.filter(org => {
          return (this.user.organizations || []).includes(org.value) || !org.value
        })
        : orgs
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isLoading() {
      return this.$store.getters.isLoading
    }
  }
};

</script>
<style>
  .collapsed > .when-open,
  .not-collapsed > .when-closed {
    display: none;
  }
</style>
