<template>
  <b-container fluid>
    <PageHeader header="Reset Your Password" lead="" />

    <b-form v-if="token">
      <b-card
        header="Reset Password"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-form-group id="fieldset-user-reset-password">
          <b-form-group
            id="input-group-user-email"
            label="Email"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-email"
          >
            <b-form-input
              id="input-user-email"
              v-model="email"
              autocomplete="off"
              :state="validate('email')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('email')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-password"
            label="Password"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-password"
          >
            <b-form-input
              type="password"
              id="input-user-password"
              v-model="password"
              autocomplete="off"
              :state="validate('password')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('password')">
              This field is required. Minimum ten characters, at least one uppercase letter,
              one lowercase letter, one number and one special character (@$!%*#?&)
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-password-confirmation"
            label="Password Confirmation"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-password-confirmation"
          >
            <b-form-input
              type="password"
              id="input-user-password-confirmation"
              v-model="password_confirmation"
              autocomplete="off"
              :state="validate('password_confirmation')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('password_confirmation')">
              This field is required. Passwords must match.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-button
            variant="success"
            :disabled="processing || !validate('all')"
            @click="submit()">
            <b-spinner class="ml-3" small v-if="processing" />
            Reset Password
          </b-button>

        </b-form-group>

      </b-card>
    </b-form>
    <b-alert v-else show variant="danger">
      Password reset token is not defined.
    </b-alert>
  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { resetPassword, logoutUser } from "@/services/auth.services";
import { validateEmail, validatePassword } from "@/services/validation.services";

export default {
  head: {
    title: 'Reset User Password',
  },
  components: {
    PageHeader
  },
  data() {
    return {
      processing: false,
      token: '',
      email: '',
      password: '',
      password_confirmation: ''
    }
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated
    },
    validate(){
      return (field) => {
        const fields = {
          email: ()=>{
            return validateEmail(this.email)
          },
          password: ()=>{
            return validatePassword(this.password)
          },
          password_confirmation: ()=>{
            return validatePassword(this.password) && (this.password === this.password_confirmation)
          }
        }
        if (fields[field] === 'undefined') return false;
        // check if all fields validate
        if (field === 'all') return Object.keys(fields || {}).filter(key => {
          return !fields[key]()
        }).length === 0;
        return !!fields[field]();
      }
    }
  },
  methods: {
    async submit() {
      this.processing = true
      const [error, result] = await resetPassword({
        token: this.token,
        email: this.email,
        password: this.password,
        password_confirmation: this.password_confirmation
      })
      if (error) {
        console.warn('Error', error, result)
        this.$store.commit('setMessage', {
            text: 'Password could not be reset. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        // successful update
        this.$store.commit('setMessage', {
          text: 'Password successfully updated',
          type: 'success'
        })
        // redirect to login page
        await this.$router.push(`/login`)
      }
      this.processing = false
    }
  },
  async mounted() {
    // sign out user
    if (this.isAuthenticated) {
      this.$store.commit('setMessage', {text: "Signing out...", type: "info"})
      const [error,] = await logoutUser()
      if (error) console.warn(error)
      this.$store.commit(
        'setMessage',
        error ? error : {text: "Signed Out Successfully", type: "success"}
      )
      if (!error) {
        this.$store.commit('setUser', null)
      }
      this.$store.commit('setLoading', false)
    }

    // set up password reset
    this.token = this.$route.params.token || ''
    this.email = this.$route.query.email
    this.password = ''
    if (!this.token) this.$store.commit(
      'setMessage',
      {text: 'Reset token not defined.', type: 'danger'}
    )
  }
}
</script>
