<template>
  <b-container fluid>
    <PageHeader header="Forgot Password" lead="Request a password reset link." />

    <b-form v-if="!submitted">
      <b-card
        header="Request Password Reset"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-form-group id="fieldset-user-reset-password">
          <b-form-group
            id="input-group-user-email"
            label="Email"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-email"
          >
            <b-form-input
              id="input-user-email"
              v-model="email"
              autocomplete="off"
              :state="validate('email')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('email')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-button
            variant="success"
            :disabled="processing || !validate('all')"
            @click="submit()">
            <b-spinner class="ml-3" small v-if="processing" />
            Send Password Reset Link
          </b-button>

        </b-form-group>

      </b-card>
    </b-form>

    <b-alert v-else show variant="info">
      A password reset link has been sent to your email address.
    </b-alert>

  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { requestPasswordReset } from "@/services/auth.services";
import { validateEmail } from "@/services/validation.services";

export default {
  head: {
    title: 'Request Reset Password Link',
  },
  components: {
    PageHeader
  },
  data() {
    return {
      processing: false,
      email: '',
      submitted: false
    }
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated
    },
    validate(){
      return (field) => {
        const fields = {
          email: ()=>{
            return validateEmail(this.email)
          }
        }
        if (fields[field] === 'undefined') return false;
        // check if all fields validate
        if (field === 'all') return Object.keys(fields || {}).filter(key => {
          return !fields[key]()
        }).length === 0;
        return !!fields[field]();
      }
    }
  },
  methods: {
    async submit() {
      this.processing = true
      const [error, result] = await requestPasswordReset('forgot-password', { email: this.email })
      if (error) {
        console.warn('Error', error, result)
        this.$store.commit('setMessage', {
            text: 'Password link could not be sent. This email may not be registered.',
            type: 'danger'
          }
        )
      }
      else {
        // successful update
        this.$store.commit('setMessage', {
          text: 'Thank you! A password reset link will be sent if this user email exists.',
          type: 'success'
        })
        this.submitted = true
      }
      this.processing = false
    }
  }
}
</script>
