<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-modal
      id="modal-create-setting"
      ref="modal"
      title="Create Global Setting"
      @ok="create()"
    >
      <template #modal-footer="{ ok, cancel }">
        <b-button size="sm" variant="info" @click="ok()">
          Create Setting
        </b-button>
        <b-button size="sm" variant="secondary" @click="cancel()">
          Cancel
        </b-button>
      </template>
      <b-form-group
        id="input-group-setting"
        label="Settings Key"
        label-class="font-weight-bold"
        :label-for="`input-global-setting-key`"
      >
        <b-form-input
          :id="`input-global-setting-key`"
          v-model="newSetting.key"
          :disabled="processing"
        >
        </b-form-input>
      </b-form-group>
      <b-form-group
        id="input-group-setting"
        label="Value"
        label-class="font-weight-bold"
        :label-for="`input-global-setting-value`"
      >
        <b-form-input
          :id="`input-global-setting-value`"
          v-model="newSetting.value"
          :disabled="processing"
        >
        </b-form-input>
      </b-form-group>

    </b-modal>

    <b-container fluid align="right">
      <b-row>
        <b-col>
          <b-button
            size="sm"
            class="m-1"
            :disabled="processing"
            v-b-modal.modal-create-setting
          >
            Create Setting <b-spinner v-if="processing" class="mr-3 ml-3" small />
          </b-button>
        </b-col>
      </b-row>
    </b-container>
    <b-table
      primary-key="id"
      :items="items"
      :fields="fields"
      striped
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      responsive="sm"
      :busy.sync="loading"
    >
      <template #cell(value)="row">
        <b-form-group
          :id="`input-group-global-setting-${row.item.id}`"
          label-class="font-weight-bold"
          :label-for="`input-global-setting-${row.item.id}`"
        >
          <b-form-input
            :id="`input-global-setting-${row.item.id}`"
            v-model="row.item.value"
            :disabled="processing"
          >
          </b-form-input>
        </b-form-group>
      </template>

      <template #cell(details)="row">
        <b-button
          size="sm"
          class="m-1"
          :disabled="processing"
          @click="update(row.item.id, row.item.value)"
        >
          Update <b-spinner v-if="processing" class="mr-3 ml-3" small />
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          :disabled="true"
        >
          Delete
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import optionsServices from "@/services/options.services";
import {getSettings, createSetting, updateSetting} from "@/services/auth.services";

export default {
  name: 'permissions-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader
  },
  data () {
    return {
      newSetting: {key: '', value: ''},
      processing: false,
      loading: false,
      error: false,
      message: '',
      sortBy: 'key',
      sortDesc: true,
      items: [],
      fields: [
        {key: 'key'},
        {key: 'value'},
        {key: 'details', label: ''}
      ]
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    }
  },
  methods: {
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    async loadSettings () {
      const [error, settings] = await getSettings()
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      this.items = settings
    },
    async create() {
      this.processing = true
      const [error, ] = await createSetting({key: this.newSetting.key, value: this.newSetting.value})
      if (error) {
        this.$store.commit('setMessage', {
            text: 'Setting could not be created. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        this.$store.commit('setMessage', {
            text: 'Setting created successfully!',
            type: 'success'
          }
        )
      }
      // reload data
      this.newSetting = {key: '', value: ''}
      await this.loadSettings()
      this.$bvModal.hide('modal-create-setting')
      this.processing = false
    },
    async update(id, value) {
      this.processing = true
      const [error, ] = await updateSetting(id, {value: value})
      if (error) {
        this.$store.commit('setMessage', {
            text: 'Setting could not be saved. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        this.$store.commit('setMessage', {
            text: 'Settings updated successfully!',
            type: 'success'
          }
        )
      }
      // reload data
      await this.loadSettings()
      this.processing = false
    }
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    await this.loadSettings()
    this.$store.commit('setLoading', false)
  }
}
</script>
