<template>
  <b-card
    :header="header"
    header-tag="header"
    header-class="font-weight-bold"
    header-bg-variant="info"
    header-text-variant="light"
    class="mb-3 mt-3"
  >
    <b-alert v-if="loading" show variant="secondary" class="w-100">
      <b-spinner class="mr-3" small /> Please wait while options are loaded...
    </b-alert>

    <b-form-group v-else>
      <b-form-group
        id="fieldset-accessibility-options"
        label="Accessibility Options"
        label-for="input-accessibility-options"
        label-class="font-weight-bold"
      >
        <b-form-checkbox-group
          id="input-accessibility-options"
          name="accessibility-options"
          :disabled="false"
          v-model="accommodations.accessibility"
          stacked
        >
          <b-form-checkbox
            v-for="(accommodation, index) in options.accessibility"
            v-bind:key="index"
            :value="accommodation.id"
            :disabled="isSubmitted"
          >{{accommodation.full_name}}
            <p class="small" v-if="accommodation.description">{{accommodation.description}}</p>
          </b-form-checkbox>
        </b-form-checkbox-group>
      </b-form-group>

      <b-form-group
        id="fieldset-dietary-options"
        label="Dietary Options"
        label-for="input-dietary-options"
        label-class="font-weight-bold"
      >
        <b-form-checkbox-group
          id="input-dietary-options"
          name="dietary-options"
          :disabled="false"
          v-model="accommodations.dietary"
          stacked
        >
          <b-form-checkbox
            v-for="(accommodation, index) in options.dietary"
            v-bind:key="index"
            :value="accommodation.id"
            :disabled="isSubmitted"
          >{{accommodation.full_name}}
            <p class="small" v-if="accommodation.description">{{accommodation.description}}</p>
          </b-form-checkbox>
        </b-form-checkbox-group>

      </b-form-group>

    </b-form-group>
  </b-card>

</template>

<script>

import {getAccommodations} from "@/services/api.services";

export default {
  name: "input-accommodations",
  props: ['id', 'header'],
  components: {},
  data () {
    return {
      loading: false,
      error: false,
      options: {},
    }
  },
  computed: {
    formData() {
      return this.$store.getters.getFormData
    },
    accommodations() {
      return this.formData[this.id] ? this.formData[this.id] : []
    },
    isSubmitted() {
      return this.$store.getters.isSubmitted;
    }
  },
  methods: {
    async loadOptions () {
      this.processing = true
      this.error = false
      const [error, data] = await getAccommodations()
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      this.options = data
      this.processing = false
    },
    async reroute(uri) {
      await this.$router.push(uri)
    }
  },
  async mounted() {
    this.loading = true
    await this.loadOptions()
    this.loading = false
  }
}
</script>
